import { sha512 } from 'js-sha512'
import {ProviderService} from '../../services/provider.service';;

export class Checks {


    static MIN_PASSWORD_LENGTH: Number = 6;
    static MAX_PASSWORD_LENGTH: Number = 100;

    static checkCredentials(email, password){
        return this.checkEmail(email) && this.checkPassword(password);
    }

    static checkBoolean(value){
        return value == true || value == false;
    }

    static checkEmail(email){
        return String(email)
        .toLowerCase()
        .match(
            /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/ 
        );
    }

    static checkPassword(password){
        return password && password.length >= this.MIN_PASSWORD_LENGTH && password.length <= this.MAX_PASSWORD_LENGTH;
    }

    static checkName(name){
        return String(name).match(/^[a-zA-Z0-9_]{3,50}$/);
    }

    static checkNickname(nickname){
        nickname = String(nickname).trim();
        return nickname.length>0;
    }

    static checkData(data){
        let date = new Date(data);
        return date instanceof Date && !isNaN(date.getTime());
    }

    static checkPhone(phone){
        return String(phone).match(/^\d{9}$/);
    }

    static checkBase64(base64){
        return String(base64).match(/^([A-Za-z0-9+/]{4})*([A-Za-z0-9+/]{4}|[A-Za-z0-9+/]{3}=|[A-Za-z0-9+/]{2}==)$/);
    }

    static checkID(id){
        const regexExp = /^[0-9a-fA-F]{8}\b-[0-9a-fA-F]{4}\b-[0-9a-fA-F]{4}\b-[0-9a-fA-F]{4}\b-[0-9a-fA-F]{12}$/gi;
        return regexExp.test(id);
    }

    static EncryptPassword(password){
        return sha512(password);
    }

}