import { MainService } from '../../services/main.service';
import { Utils } from '../../services/utils.service';
import { Component, OnInit, Input } from '@angular/core';

@Component({
    selector: 'app-publicacio-info',
    templateUrl: './publicacio-info.component.html',
    styleUrls: ['./publicacio-info.component.scss']
})
export class PublicacioInfoComponent implements OnInit {

    @Input() idPost;
    @Input() idObra;
    @Input() p;

    public utils = Utils;

    constructor(public main: MainService) { }

    async ngOnInit() {
        // TACAT (skeleton (?)) //
        if (this.idPost) this.p = await this.main.provider.getPost(this.idPost);
        if (this.idObra) this.p = await this.main.provider.getArtwork(this.idObra);
        console.log(this.p);
        
    }

}
