import { MainService } from './../../services/main.service';

import { Component, OnInit } from '@angular/core';

import SwiperCore, { Navigation, Pagination, Scrollbar, Virtual } from 'swiper';
import { Utils } from '../../services/utils.service';
SwiperCore.use([Navigation, Pagination, Scrollbar, Virtual]);

@Component({
    selector: 'app-appbar',
    templateUrl: './appbar.component.html',
    styleUrls: ['./appbar.component.scss']
})
export class AppBarComponent implements OnInit {

    public tooltipProfileObert = false;

    constructor(public main: MainService) {
        main.appbar = this;
    }

    async ngOnInit() {

        // Close the dropdown menu if the user clicks outside of it
        window.addEventListener("click", (event) => {
            if (!event.target["matches"]('.profile img'))
                this.tooltipProfileObert = false;
        });
    }

    public logoClickGoTo(){

        if(Utils.getToken()){ //En el cas de que tingui token, anir a la home, en el cas contrari, a la pgina de login
            this.main.goToHome();
        }else{
            this.main.goToLogin();
        }

    }

    public profileTooltipToggle() {
        this.tooltipProfileObert = !this.tooltipProfileObert;
    }

}
