import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ContentComponent } from "./shared/components/layout/content/content.component";
import { FullComponent } from "./shared/components/layout/full/full.component";
import { full } from "./shared/routes/full.routes";
import { content } from "./shared/routes/routes";

import { AdminGuard } from './shared/guard/admin.guard';
import { ActivateComponent } from './in2art/pages/auth/activate/activate.component';
import { ForgetPasswordComponent } from './pages/authentication/forget-password/forget-password.component';
import { ResetPasswordComponent } from './in2art/pages/auth/reset-password/reset-password.component';
import { RecoverPasswordComponent } from './in2art/pages/auth/recover-password/recover-password.component';
// import { LoginComponent } from 'src/app/in2art/pages/login/login.component';
// import { LogoutComponent } from 'src/app/in2art/pages/auth/logout/logout.component';

const routes: Routes = [
    // {
    //     path: '',
    //     redirectTo: 'home',
    //     pathMatch: 'full'
    // },


    // {
    //     path: 'login',
    //     component: LoginComponent
    // },
    // {
    //     path: 'logout',
    //     component: LogoutComponent
    // },
    {
        path: '',
        component: ContentComponent,
        canActivate: [AdminGuard],
        children: content
    },
    {
        path: '',
        component: FullComponent,
        canActivate: [AdminGuard],
        children: full
    },
    {
        path:'auth/activate/:id',
        component: ActivateComponent
    },
    {
        path: 'auth/resetpassword/:token',
        component: RecoverPasswordComponent
    },
    {
        path: '**',
        redirectTo: ''
    }
];

@NgModule({
    imports: [[RouterModule.forRoot(routes, {
        anchorScrolling: 'enabled',
        scrollPositionRestoration: 'enabled',
        relativeLinkResolution: 'legacy'
    })],
    ],
    exports: [RouterModule]
})
export class AppRoutingModule { }
