import { Notificacio } from './../../classes/notificacio';
import { Component, Input, OnInit } from '@angular/core';
import { MainService } from '../../services/main.service';

@Component({
    selector: 'app-notification',
    templateUrl: './notification.component.html',
    styleUrls: ['./notification.component.scss']
})
export class NotificationComponent implements OnInit {

    @Input() notification: Notificacio;
    public user;

    public fotoSistema = "assets/in2art/logos/in2.svg";

    constructor(public main: MainService) { }

    ngOnInit(): void {
        this.notification = this.notification || this.main.getParams();
        
        this.user = this.notification.user;
        if (this.notification.msgInfo.msgType == 2) {
            // Missatges del sistema //
            this.user = {
                photo: this.fotoSistema,
                username: "In2Art",
            }
        }
    }

    setReviewed(idNotification: number) {
        // console.log(idNotification); //undefined
        this.main.provider.setNotificationReviewed(idNotification);
     }

}
