import { Component, Input, OnInit } from '@angular/core';
import { MainService } from '../../services/main.service';
import { ProviderService } from '../../services/provider.service';

@Component({
    selector: 'app-notification-dot',
    templateUrl: './notification-dot.component.html',
    styleUrls: ['./notification-dot.component.scss']
})
export class NotificationDotComponent implements OnInit {
  
    @Input() num;
    @Input() showNum;

    actualNotifications = [];

    constructor(public main: MainService, public provider: ProviderService) { }

    ngOnInit(): void {
        this.isNotificationReviwed();
        this.getNumText();
        this.getTotalNum();
    }

    getTotalNum() {
        this.num = this.actualNotifications.length;
    }

    getNumText() {
        if (this.num > 9) return "9+";
        return this.num;
    }

    isNotificationReviwed() { //Falta excluir les notificacions que ja estan mortes.
        this.main.provider.getAllUserNotification(this.main.currentUser.id).then((data) => {
            this.num = data["length"];
        });

    }
}
