import { Component, OnInit, Input, ViewChild, HostListener } from '@angular/core';
import { min } from 'rxjs/operators';

@Component({
  selector: 'app-skeleton-loader',
  templateUrl: './skeleton-loader.component.html',
  styleUrls: ['./skeleton-loader.component.scss']
})
export class SkeletonLoaderComponent implements OnInit {

  @Input() numero;
  @ViewChild('bloc') bloc;
  @Input('cols.xs') cols_xs: number; //col < 576px
  @Input('cols.sm') cols_sm: number; //col >= 576px
  @Input('cols.md') cols_md: number; //col >= 768px
  @Input('cols.lg') cols_lg: number; //col >= 992px
  @Input('cols.xl') cols_xl: number; //col >= 1200px
  @Input() rowHeight: number = 1;
  @Input() gutterSize: number = 5;
  emptyImages = [];
  public rows: number = 0;

  
  constructor() {}

  ngOnInit(): void {
    this.setSkeletonIMGSize();
  }

  setSkeletonIMGSize() { 

    let min = 100;
    let max = 1000;

    for (var i = 0; i < 20; i++) {
      let width = Math.round(Math.random() * (max - min)) + min;
      let height = Math.round(Math.random() * (max - min)) + min;

      this.emptyImages.push({ id: i, Swidth: width, Sheight: height });
    }
    
  }

  getDefaultSize() { 

    var usersWidth = window.innerWidth;
    
    if (usersWidth < 576)
      return this.cols_xs;
    else if (usersWidth >= 576 && usersWidth < 768)
      return this.cols_sm;
    else if (usersWidth >= 768 && usersWidth < 992)
      return this.cols_md;
    else if (usersWidth >= 992 && usersWidth < 1200)
      return this.cols_lg;
    else if (usersWidth > 1200)
      return this.cols_xl;
  }

}
