import { Utils } from 'src/app/in2art/services/utils.service';
import { MainService } from '../../services/main.service';
import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'app-splash-screen',
    templateUrl: './splash-screen.component.html',
    styleUrls: ['./splash-screen.component.scss']
})
export class SplashScreenComponent implements OnInit {

    public pathsNoMostrar = ["/settings/about/privacy-policy"];

    public mostrar;
    public timeoutRef;

    constructor(public main: MainService) {
        main.splashScreen = this;
    }

    async ngOnInit() {
        if (Utils.sessionStorageGet("splashScreenShown")) return;
        if (this.pathsNoMostrar.includes(window.location.pathname)) return;

        this.show();

        this.timeoutRef = setTimeout(() => {
            this.hide();
        }, 2000);
    }

    show() {
        this.mostrar = true;
    }
    hide() {
        clearTimeout(this.timeoutRef);
        this.mostrar = false;
        Utils.sessionStorageSet("splashScreenShown", 1);
    }

}
