import { MainService } from './main.service';
import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class ErrorHandlerService {

    constructor(private main: MainService) {}

    public mostrarError(data) {
        if (!data.code) return false;
        if (data.code == 1) return false;

        console.error(data);
        // this.main.toastDanger(`Error ${data.code}: ${data.message}`);
        if (data.message || data.msg)
            this.main.toastDanger(data.message || data.msg);
        return true;
    }
    public hiHaError(data) {
        if (!data.code) return false;
        if (data.code == 1) return false;

        // console.log(data);
        return true;
    }

    public peticioFallada(error) {
        console.log("Peticio fallada:", error);
        // this.main.toastDanger(`Error ${error.status}: ${error.statusText}`);

        if (error.status == 401) this.unauthorized();
    }

    public unauthorized() {
        // console.log("peticioFallada, unauthorized, redirigint a /login");
        // this.main.toastDanger("Unauthorized");
        console.error("Unauthorized");
        // this.main.goTo("/login");
    }

}
