import { BarraRatingComponent } from './barra-rating/barra-rating.component';
import { Component, OnInit, QueryList, ViewChild, ViewChildren, Input } from '@angular/core';
import { MainService } from '../../../services/main.service';

@Component({
    selector: 'app-rating',
    templateUrl: './rating.component.html',
    styleUrls: ['./rating.component.scss']
})
export class RatingComponent implements OnInit {

    @Input() idObra;

    @ViewChildren("barraRating") barresRating: QueryList<BarraRatingComponent>;

    public valoracio;
    public carregant = false;

    constructor(public main: MainService) { }

    async ngOnInit() {
        this.valoracio = { mitjanaObra: 0, criteris: [] };
        await this.getRating();
    }

    async getRating() {
        // this.valoracio = await this.main.provider.getValoracio(this.idObra);
        this.valoracio = 0;
    }

    mostrarBotoVotar() {
        if (!this.barresRating) return false;
        let barres = this.barresRating.toArray();
        for (let i = 0; i < barres.length; i++)
            if (barres[i].modificat && !barres[i].hasVotat)
                return true;
    }
    deshabilitarBotoVotar() {
        if (!this.barresRating) return false;
        let barres = this.barresRating.toArray();
        for (let i = 0; i < barres.length; i++)
            if (barres[i].carregant)
                return true;
    }
    async submit() {
        let barres = this.barresRating.toArray();
        for (let i = 0; i < barres.length; i++) {
            barres[i].submit(async () => {
                await this.getRating();
            });
        }
    }
}
