import { MainService } from './main.service';
import { Injectable } from '@angular/core';
import { Utils } from './utils.service';


@Injectable({
    providedIn: 'root'
})
export class NotificationCheckerService {

    public frequencia;
    public intervalRef;

    constructor(private main: MainService) { }

    public start(freq = 5000) {
        this.frequencia = freq;

        if (this.intervalRef) this.stop();
        this.intervalRef = setInterval(this.peticioNotificacions, freq);

    }
    public stop() {
        clearInterval(this.intervalRef);
    }
    public peticioNotificacions() {
        if (!Utils.getToken()) return;

        //this.main.toastNotificacio({ type: 1, message: 'followed you', fromUser: 3, username: 'jordi', date: '2022-03-15 02:45:45', isAlive: true, isReviewed: false },)

        //this.main.toastInfo("holis");
    }
}
