import { Routes } from '@angular/router';


export const content: Routes = [
    // {
    //   path: '',
    //   loadChildren: () => import('src/app/in2art/pages/in2art-routing.module').then(m => m.In2artRoutingModule)
    // },
    {
        path: 'home',
        redirectTo: ''
    },
    {
        path: 'presentation',
        loadChildren: () => import ('src/app/in2art/pages/presentation/presentation.module').then(m => m.PresentationModule)
    },
    {
        path: 'search',
        loadChildren: () => import ('src/app/in2art/pages/search/search.module').then(m => m.SearchModule)
    },
    {
        path: 'learn',
        loadChildren: () => import('src/app/in2art/pages/learn/learn.module').then(m => m.LearnModule)
    },
    {
        path: 'edit-profile',
        loadChildren: () => import('src/app/in2art/pages/edit-profile/edit-profile.module').then(m => m.EditProfileModule)
    },
    {
        path: 'new',
        loadChildren: () => import('src/app/in2art/pages/new/new.module').then(m => m.NewModule)
    },
    {
        path: 'artwork/:idObra',
        loadChildren: () => import('src/app/in2art/pages/publicacio-artwork/publicacio-artwork.module').then(m => m.PublicacioArtworkModule)
    },
    {
        path: 'post/:idPost',
        loadChildren: () => import('src/app/in2art/pages/publicacio-post/publicacio-post.module').then(m => m.PublicacioPostModule)
    },
    {
        path: 'ytvideo/:id',
        loadChildren: () => import('src/app/in2art/pages/ytvideo/ytvideo.module').then(m => m.YtvideoModule)
    },
    {
        path: 'settings',
        loadChildren: () => import('src/app/in2art/pages/settings/settings.module').then(m => m.SettingsModule)
    },
    {
        path: 'shop',
        loadChildren: () => import('src/app/in2art/pages/art-shop/art-shop-routing.module').then(m => m.ArtShopRoutingModule)
    },
    { path: 'login', redirectTo: 'auth/login' },
    { path: 'logout', redirectTo: 'auth/logout' },
    { path: 'signup', redirectTo: 'auth/signup' },
    { path: 'activate', redirectTo: 'auth/activate' },
    { path: 'verifyaccount/:key', redirectTo: 'auth/verifyaccount/:key' },
    { path: 'recoverpassword/:key', redirectTo: 'auth/recoverpassword/:key' },
    { path: 'resetpassword', redirectTo: 'auth/resetpassword' },
    {
        path: 'auth',
        loadChildren: () => import('src/app/in2art/pages/auth/auth.module').then(m => m.AuthModule)
    },
    {
        path: 'welcome',
        loadChildren: () => import('src/app/in2art/pages/welcome/welcome.module').then(m => m.WelcomeModule)
    },
    {
        path: 'interests',
        loadChildren: () => import('src/app/in2art/pages/interests/interests.module').then(m => m.InterestsModule)
    },
    {
        path: 'notifications',
        loadChildren: () => import('src/app/in2art/pages/notifications/notifications-routing.module').then(m => m.NotificationsRoutingModule)
    },
    {
        path: 'test',
        loadChildren: () => import('src/app/in2art/pages/test/test.module').then(m => m.TestModule)
    },
    {
        path: 'profile',
        loadChildren: () => import('src/app/in2art/pages/profile-redirect/profile-redirect.module').then(m => m.ProfileRedirectModule)
    },
    {
        path: ':username',
        loadChildren: () => import('src/app/in2art/pages/profile/profile.module').then(m => m.ProfileModule)
    },
    {
        path: '',
        pathMatch: 'full',
        loadChildren: () => import('src/app/in2art/pages/home/home.module').then(m => m.HomeModule)
    },
];
