
import { MainService } from '../../services/main.service';

import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'app-selector-idioma',
    templateUrl: './selector-idioma.component.html',
    styleUrls: ['./selector-idioma.component.scss']
})
export class SelectorIdiomaComponent implements OnInit {

    // Probablement totes les banderes que hi ha //
    // https://github.com/lipis/flag-icons/tree/main/flags/1x1 //

    public idioma;
    public idiomes = [
        { codi: "ca", icona: "flag-icon-es-ct", nom: "Catal" },
        { codi: "es", icona: "flag-icon-es", nom: "Espaol" },
        { codi: "en", icona: "flag-icon-us", nom: "English" },
    ];
    public bandera;

    constructor(public main: MainService) { }

    async ngOnInit() {
        // Establir valor idioma //
        this.idioma = this.main.getLocale();

        this.bandera = this.idiomes.filter((i) => this.idioma == i.codi)[0]?.icona;
    }

    canviarIdioma() {
        // console.log(this.main.urlBase + "/" + this.idioma);
        
        location.href = this.main.urlBase + "/" + this.idioma;
    }
}
