import { MainService } from '../../services/main.service';
import { Component, Injectable, OnInit, ViewChild } from '@angular/core';
import { ModalManager } from 'ngb-modal';
import { ProviderService } from '../../services/provider.service';

@Component({
    selector: 'app-modal-report',
    templateUrl: './modal-report.component.html',
    styleUrls: ['./modal-report.component.scss']
})
    
@Injectable({
    providedIn: 'root'
})
    
export class ModalReportComponent implements OnInit {

    @ViewChild("modalReport") modal;
    @ViewChild("textarea") comentari;

    public id;
    public type; // 1=Publicacio, 2=Comentari, 3=Perfil //

    //Arrays
    arrayReports;
    subReports: Array<any>;

    //Control de pagina del modal
    modalpageContent = 1;
    modalTitle: string;
    carregant =  true;

    //Arrays per guardar valors
    father = [];
    child = [];


    constructor(
        private main: MainService,
        private modalService: ModalManager,
        private provider: ProviderService
    ) {
        main.modalReport = this;
    }
    async ngOnInit() { }
    

    controlBackArrow() {
        if (this.modalpageContent == 3)
            this.child = [];
        if (this.modalpageContent == 2)
            this.father = [];
            
        if (this.modalpageContent != 1) {
            if (this.subReports.length == 0)
                this.modalpageContent = 1;
            else
                this.modalpageContent -= 1;
        }
    }

    /**
     * Funcio per automatitzar els titols que tenen cada pagina del modal de reports
     * @returns string
     */
    titleType() {

        switch (this.modalpageContent) {
            
            case 1:
                this.modalTitle = "Main Report reason";
                break;
        
            case 2:
                this.modalTitle = "Sub Reports reason";
                break;
            
            case 3:
                this.modalTitle = "Report reason";
                break;
        }
        return this.modalTitle;
    }

    createReportObject(type: number, idType: number, index: number) {

        switch (type) {
            
            case 1: //Main report
                let mainName = this.arrayReports[index].tipus;
                let newMain = { id: idType, nom: mainName, indexArray: index };
                this.father[0] = newMain;
                break;
            
            case 2: //Sub report
                let subName = this.subReports[index].tipus;
                let newSub = { id: idType, nom: subName, indexPos: index };
                this.child[0] = newSub;
                break;
        }

    }

    getSubTypes(idType: number) {
        let index = this.arrayReports.findIndex(x => x.id === idType); //Obtenim la posicio del objecte dins del array

        if (this.arrayReports[index].subTipus.length >= 1) { //Condicional per controlar que el subtipus te contingut
            this.subReports = this.arrayReports[index].subTipus;
            this.createReportObject(1, idType, index);
            this.modalpageContent = 2;
        } else { //En el cas de que el report seleccionat  no tingui cap fill redigirem el usuari a la pagina 3
            this.createReportObject(1, idType, index);
            this.child = [{ id: null, nom: null, indexArray: null }];
            this.modalpageContent = 3;
            this.subReports = [];
        }
    }

    getFormReport(idSubType: number) {
        let indexPos = this.subReports.findIndex(x => x.id_denuncia_tipus === idSubType);
        this.createReportObject(2, idSubType, indexPos);
        this.modalpageContent = 3;
    }
    
    resetVars() {
        delete this.id;
        delete this.type;

        this.subReports = [];
        this.father = [];
        this.child = [];
        this.modalpageContent = 1;
        this.comentari = "";
    }
    
    submitReport() { 
        var idType;

        if (this.child[0].id == null)
            idType = this.father[0].id;
        else
            idType = this.child[0].id;
        
        this.decideReportPs(idType);
    }

    decideReportPs(idType: number) {

        switch (this.type) {
            case 1: //Post
                this.provider.addReportPost(this.id, idType, this.comentari);
                break;
        
            case 2: //Comment
                this.provider.addReportComment(this.id, idType, this.comentari);
                break;
            
            case 3: //Profile
                this.provider.addReportProfile(this.id, idType, this.comentari);
                break;
        }

    }

    /*******************
     * MODAL FUNCTIONS *
     *******************/

    async openModal(id, type) {

        this.id = id;
        this.type = type;

        this.modalService.open(this.modal, {
            size: "lg",
            modalClass: "llista",
            backdropClass: "modal-backdrop",
            hideCloseButton: true,
            centered: true,
            backdrop: true,
            animation: true,
            keyboard: true,
            closeOnOutsideClick: false,
        });
        this.carregant = true;
        this.arrayReports = await this.provider.getAllReportTypes(); //type
        this.carregant = false;
    }

    closeModal() {
        this.modalService.close(this.modal);
        this.resetVars();
    }
}
