import { MainService } from '../../services/main.service';

import { Component, OnInit, Input } from '@angular/core';

@Component({
    selector: 'app-time-ago',
    templateUrl: './time-ago.component.html',
    styleUrls: ['./time-ago.component.scss']
})
export class TimeAgoComponent implements OnInit {

    @Input() data;

    public text = "";
    public textMaco = "";
    public readonly TEMPSACT = 1000;

    constructor(public main: MainService) { }

    async ngOnInit() {
        setInterval(() => {

            if (this.data == null) return;
            this.data = new Date(this.data);
            if (isNaN(this.data)) return;
            this.text = this.calculateDate(this.data);

            this.textMaco = this.data.toLocaleTimeString(this.main.getLocale(), { timeStyle: "medium" }) + ", " + this.data.toLocaleDateString(this.main.getLocale(), { dateStyle: "long" });
            // this.textMaco = this.data.toLocaleString('en-gb', { dateStyle: "long", timeStyle: "medium" });

        }, this.TEMPSACT);
    }

    public calculateDate(date) {
        let miliseconds = new Date().getTime() - date.getTime();

        if (miliseconds < 0) return "";

        let time = miliseconds / (1000 * 60 * 60 * 24);
        if (time >= 1) return Math.floor(time) + "d";

        time = (miliseconds % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60);
        if (time < 24 && time > 1) return Math.floor(time) + "h";

        time = (miliseconds % (1000 * 60 * 60)) / (1000 * 60);
        if (time < 59 && time > 1) return Math.floor(time) + "m";

        time = (miliseconds % (1000 * 60)) / 1000;
        return Math.floor(time) + "s"
    }

}
