import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';
import { MainService } from '../../../../services/main.service';

@Component({
    selector: 'app-comment',
    templateUrl: './comment.component.html',
    styleUrls: ['./comment.component.scss']
})
export class CommentComponent implements OnInit {
    @Input() item;
    @Input() pare;
    @Input() showButtonAnswers;
    @Input() idPost;
    
    @ViewChild("commentInfo") commentInfo: ElementRef;
    
    constructor(public main: MainService) { }

    async ngOnInit() {
        this.item.showAnswers = false;
        if (!this.item.user) this.item.user = await this.main.provider.getUserById(this.item.user_id);
      
    }
    openOpcionsComentarisModal(idComment: number, user_id: string) {
        
        // Definir opcions //
        let share = {
            accio: () => { this.main.notInMyBeta(); },
            img: "assets/in2art/icones/share.svg",
            text: "Share comment",
            class: "share",
        };
        let copy = {
            accio: () => {
                this.closeOpcionsComentarisModal();
                this.main.copy(`${this.main.urlBase}/post/${this.idPost}?comment=${idComment}`);
            },
            img: "assets/in2art/icones/link.svg",
            text: this.main.t("Copy URL"),
            class: "copy",
        };
        let report = {
            accio: () => {
                this.closeOpcionsComentarisModal();
                this.main.modalReport.openModal(idComment, 2);
            },
            img: "assets/in2art/icones/report.svg",
            text: "Report",
            class: "report",
        };

        // Aplicar opcions //
        let opcions = [];

        if (window.isSecureContext) {
            opcions.push(copy);
        }
        if (user_id == this.main.currentUser.id) { }
        else
            opcions.push(report);
        
        this.main.modalOptions.openModal(opcions);
    }
    closeOpcionsComentarisModal() { this.main.modalOptions.closeModal(); }
}
