import { Component, OnInit } from '@angular/core';

@Component({
    selector: 'app-boto-like',
    templateUrl: './boto-like.component.html',
    styleUrls: ['./boto-like.component.scss']
})
export class BotoLikeComponent implements OnInit {
  
    private corBuit = 'far fa-heart';
    private corPle = 'fas fa-heart';

    likeDonat = false;
    icona = this.corBuit;
  
    constructor() { }

    async ngOnInit() { }


    public botoLikeClick() {
        this.likeDonat = !this.likeDonat;

        if (this.likeDonat) this.icona = this.corPle;
        else this.icona = this.corBuit;

        // TODO: Peticio (si this.likeDonat, enviar like. Si no, teure'l) //
    }

}
