// import { Usuari } from './../../classes/usuari';
import { MainService } from '../../services/main.service';
import { Component, OnInit } from '@angular/core';
import { Notificacio } from '../../classes/notificacio';
import { ProviderService } from '../../services/provider.service';

@Component({
    selector: 'app-notifications',
    templateUrl: './notifications.component.html',
    styleUrls: ['./notifications.component.scss']
})

export class NotificationsComponent implements OnInit {

    tabActiu: number = 1;
    num: number = 0;
    carregantProfile: boolean;

    userNotifications: Array<Notificacio> = [];
    systemNotifications: Array<Notificacio> = [];
    shopNotifications: Array<Notificacio> = [];

    constructor(
        public main: MainService,
        public provider: ProviderService,
    ) {
        main.newPage(this);
    }
    async ngOnInit() {
        this.getUserNotifications();
    }

    ngOnDestroy() { this.main.onDestroyPage(); } //Necessari per si es vol obrir un modal per la part de system per explicar updates.

    async getUserNotifications() {

        let i = 0;
        let notifications, params, user, postDate: any;
        // let user = null;

        await this.main.provider.getAllUserNotification(this.main.currentUser.id).then(
            (data) => {
                notifications = data;
            }
        ) ;

        if (notifications != null) { 

            for (i = 0; i < notifications.length; i++) {
                if (notifications[i].isAlive != false) { //Controlem en el cas de que la notificacio segueixi viva

                    user = await this.main.provider.getUserById(notifications[i].from_user_notification);
                    postDate = new Date(notifications[i].date_notification);

                    params = { //Objecte amb tots els paramatres necessaris per la funcio
                        msgType: notifications[i].tipus_notification,
                        user: user, msg: notifications[i].missatge_notification,
                        date: postDate, isReviewed: notifications[i].isReviewed_notification,
                        idNotification: notifications[i].id_notification
                    }

                    this.identifyTypeOfContent(params);
                }
            }
        }
    }

    identifyTypeOfContent(params: Object) {

        let msgInfo = { msgType: params["msgType"], msg: params["msg"], date: params["date"], isReviewed: params["isReviewed"], idNotification: params["idNotification"] }

        switch (params["msgType"]) {
            case 1: //User notificaitons
                this.userNotifications.push(new Notificacio(params["user"], msgInfo));
                this.num++;
                break;

            case 2: //System notifications
                this.systemNotifications.push(new Notificacio(params["user"], msgInfo));
                this.num++;
                break;

            case 3: //Shop notifications
                this.shopNotifications.push(new Notificacio(params["user"], msgInfo));
                this.num++;
                break;

            default:
                console.log("No valid notification " + "number: " + params["msgType"] + " Object: " + params["user"] + " msg " + params["msg"]);
                break;
        }

    }
}
