import { Component, ElementRef, Input, OnInit, ViewChild } from '@angular/core';
import { MainService } from 'src/app/in2art/services/main.service';

@Component({
    selector: 'app-reactions',
    templateUrl: './reactions.component.html',
    styleUrls: ['./reactions.component.scss']
})
export class ReactionsComponent implements OnInit {

    @Input() idComment;
    @Input() comment;

    @ViewChild('selected') selected: ElementRef;
  
    constructor(public main: MainService) { }

    ngOnInit(): void {
   
    }

    submit(idReaction: string) {
        //Fer petici

        let reaction = this.selected.nativeElement;

        if (parseInt(idReaction) == 0) {
            if (reaction.hasChildNodes()) reaction.childNodes[0].remove();
            return;
        }

        let srcValue = document.getElementById(idReaction).getAttribute('src');
        let img = document.createElement('img');
    
        img.setAttribute('src', srcValue);
        img.setAttribute('height', '18px');
        img.setAttribute('width', '18px');

        if (reaction.hasChildNodes()) reaction.childNodes[0].remove();
        reaction.appendChild(img);
    }
    
}
