import { MainService } from './../../../services/main.service';
import { Component, OnInit } from '@angular/core';
import { Utils } from 'src/app/in2art/services/utils.service';
import { ActivatedRoute } from "@angular/router";
import { delay } from 'rxjs/operators';

@Component({
    selector: 'app-activate',
    templateUrl: './activate.component.html',
    styleUrls: ['./activate.component.scss']
})
export class ActivateComponent implements OnInit {

    constructor(
        public main: MainService,
        private route: ActivatedRoute) {
        main.newPage(this);
    }

    async ngOnInit() {
        // TODO: peticio activar conta //

        // this.main.toastShow("Your account has been activated");
        // // location.href = "/home";
        // this.main.goTo("/interests");

        let token = this.route.snapshot.paramMap.get("token");

        this.main.provider.activarUser(token).then(async (res) => {

            if (res) {
                this.main.toastSuccess("Your account activated successfully");
                await this.delayLogin();
                    
            } else {
                this.main.toastDanger("Error activating account");
            }

        }).catch((err) => {
            this.main.goTo("/auth/login");
        });
        
    }

    ngOnDestroy() { this.main.onDestroyPage(); }

    public async delayLogin() {
        await delay(5000);
        this.main.goTo("auth/login");
    }

}
