import { style } from '@angular/animations';
import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { MainService } from 'src/app/in2art/services/main.service';


@Component({
    selector: 'app-barra-rating',
    templateUrl: './barra-rating.component.html',
    styleUrls: ['./barra-rating.component.scss']
})
export class BarraRatingComponent implements OnInit {

    @Input() idObra;
    @Input() criteri;
    
    @ViewChild("barra") barra;

    public modificat = false;
    public hasVotat = false;
    public carregant = false;
    
    public valorRating = null;
    public valorRatingUsuari = null;


    constructor(public main: MainService) { }

    async ngOnInit() {
        this.actValoracio();
    }

    input(event) {
        this.valorRating = parseFloat(event.target.value);
        this.modificat = true;
    }

    async actValoracio(/*criteri ?*/) {
        // let res: any = await this.main.provider.getValoracio(this.id);

        if (this.criteri.valoracioUsuari === null) {
            this.valorRating = 0;
            this.hasVotat = false;
        } else {
            this.valorRating = this.criteri.valoracioMitjana;
            this.valorRatingUsuari = this.criteri.valoracioUsuari;
            this.hasVotat = true;
        }
    }

    async submit(callback) {
        if (!this.modificat || this.hasVotat) return;

        
        this.carregant = true;
            
        await this.main.provider.votar(this.idObra, this.criteri.id, this.valorRating);
        this.hasVotat = true;
        await callback(); // Actualitzar rating amb peticio //
        this.actValoracio();

        this.carregant = false;
    }
}
