import { Utils } from './../../../services/utils.service';
import { MainService } from 'src/app/in2art/services/main.service';
// import { Utils } from './../../services/utils.service';
import * as $ from "jquery";

import { Component, OnInit, ViewChild } from '@angular/core';

@Component({
    selector: 'app-acdc-datepicker',
    templateUrl: './acdc-datepicker.component.html',
    styleUrls: ['./acdc-datepicker.component.scss']
})
export class AcdcDatepickerComponent implements OnInit {

    private data;
    public type = "";
    public day;
    public month;
    public year;
    public century;
    public acdcAny = true;
    public acdcSegle = true;
    public anyActual = new Date().getFullYear();

    public textAny;
    public textSegle;

    public touched = false;

    constructor(public main: MainService) {

    }

    async ngOnInit() {
        this.main["acdc"] = this;
        // this.type = "y";
    }
    /**
     * 
     * @returns Data a llegir pel formulari
     */
    getObjData() {
        this.data = {};
        this.data.type = this.type;
        if (this.data.type == "y") {
            this.data.day = this.day;
            this.data.month = this.month;
            this.data.year = this.year;
            this.data.acdc = this.acdcAny;
        }
        else if (this.data.type == "c") {
            this.data.century = this.century;
            this.data.acdc = this.acdcSegle;
        }
        else return null;
        
        return this.data;
    }

    getTextAny() {
        if (!this.isValid()) return null;// TODO: mostrar error correctmanetn!!

        let data = this.getObjData();

        if (data.type == "y") {
            let d = new Date(0);
            d.setFullYear(data.year);
            d.setMonth((data.month || 1) - 1);
            d.setDate(data.day || 1);

            let params = {};

            if (data.day) params["day"] = "numeric";
            if (data.month) params["month"] = "long";
            if (data.year) params["year"] = "numeric";

            return d.toLocaleDateString(this.main.getLocale(), params) + " " + this.boolToAcdc(data.acdc);
        }
        if (data.type == "c") {
            return Utils.romanize(data.century) + " " + this.boolToAcdc(data.acdc);
        }
    }
    getTextSegle() {
        // Exemple: 18 -> Century XVIII AD (1701-1800 AD) //
        
        this.century = Utils.numberInRange(this.century, 1, 1000);

        let segleRoma = Utils.romanize(this.century);
        let any1 = (this.century - 1) * 100 + 1;
        let any2 = (this.century) * 100;

        // Si s abans de crist, intercanviar els anys //
        let rangAnys = this.acdcSegle ? any1 + "-" + any2 : any2 + "-" + any1;

        let acdc = this.boolToAcdc(this.acdcSegle);
        
        return `Century ${segleRoma} ${acdc} (${rangAnys} ${acdc})`;
    }
    boolToAcdc(acdc) {
        return acdc ? "AD" : "BC";
    }
    isValid() {
        if (this.type == "y") {
            if (!this.year) return false;
            if (this.day && !this.month) return false;
            // TODO: Comprovar si data valida (crear date i comprovar si el dia ha canviat (29/2 -> 1/3))
            // TODO: Comprovar si data es mes petita que avui
            // Solucio temporal //
            if (this.day && !Utils.numberIsInRange(this.day, 1, 31)) return false;
            if (this.month && !Utils.numberIsInRange(this.month, 1, 12)) return false;
            if (!Utils.numberIsInRange(this.year, 1, this.acdcAny ? this.anyActual : 250000)) return false;
        }
        if (this.type == "c") {
            if (!this.century) return false;
        }
        if (this.type == "") {
            return false;
        }
        return true;
    }
}
