import { Utils } from './../../services/utils.service';
import { MainService } from './../../services/main.service';
import * as $ from "jquery";

import { Component, OnInit, Input, ViewChild } from '@angular/core';

import { SwiperOptions } from 'swiper';
import 'is-in-viewport'; // https://github.com/zeusdeux/isInViewport //
import { SwiperComponent } from "swiper/angular";

import SwiperCore, { Navigation, Pagination, Scrollbar, Virtual } from 'swiper';
SwiperCore.use([Navigation, Pagination, Scrollbar, Virtual]);

import {ActivatedRoute} from '@angular/router';

// https://swiperjs.com/swiper-api#parameters //

@Component({
    selector: 'app-publicacio',
    templateUrl: './publicacio.component.html',
    styleUrls: ['./publicacio.component.scss']
})
export class PublicacioComponent implements OnInit {

    @Input() postId;
    @Input() p;
    @Input() idObra;

    @Input() preview: boolean = false;

    @ViewChild("commentsComponent") commentsComponent;
    @ViewChild("ratingComponent") ratingComponent;
    @ViewChild('post') post;
    @ViewChild('swiper', { static: false }) swiper?: SwiperComponent;

    public active = false;
    public viewPostEnviat = false;
    

    public alt = "";
    public swiperActiu;
    public autoplay = true;

    public viewOpen = false;
    public ratingOpen = false;
    public commentOpen = false;

    public loadingShared = false;

    // Swipe //
    swipeContentPublicacioConfig: SwiperOptions = {
        autoHeight: true,
        resistanceRatio: 0.666,
        grabCursor: false,
        // observeParents: true, // No va gaire be //
        // touchMoveStopPropagation: true, // Noms mbil //
        // watchOverflow: false,
        scrollbar: { draggable: true },
        threshold: 10,
    };

    @ViewChild("moreModal") moreModal;

    constructor(public main: MainService, public route: ActivatedRoute) { }

    async ngOnInit() {

        if (this.postId) this.p = await this.main.provider.getPost(this.postId);

        if (!this.p) return;

        console.log(this.p);
        
        this.alt = `${this.p.title || (this.p.esObra ? 'Artwork' : 'Post')} from ${this.p.author}. ${this.p.description}`;

        //TODO: ocultar dies o mes i eso... mostrar segle... Com a ACDCdatePicker
        if (!this.p.dateMaco && this.p.date)
            this.p.dateMaco = new Date(this.p.date).toLocaleDateString(this.main.getLocale(), <any>{ dateStyle: "long" });
        
        if (this.p.authorType != 1)
            if (this.p.dateMaco != "")
                this.p.dateMaco = `, (${this.p.dateMaco})`;
        
        // setInterval(() => { this.marcarPostActiu(); }, 1000);

        $("video").prop("muted", true);
        this.marcarPostActiu();
        this.reproduirVideoPostActiu();

        let _this = this;
        $(window).scroll(function () {
            _this.marcarPostActiu();
        });
        // Actualitzar autoheight per primer slide (millor aixo que res) // Ja no cal, no?
        // setTimeout(() => { this.swiper.swiperRef.updateAutoHeight(100); }, 2000);

        //Comprovem si s'ha compartit un comentari.
        
        //TODO: petici getCommentById
        if (this.route.snapshot.queryParams.comment) this.openDivComment();
    }


    getIDfromLazyData(dataArtist: any, dataCommunity: any){

        if(dataArtist == null)
            console.log("ComunnityID: ", dataCommunity);
        else if(dataCommunity == null)
            console.log("ArtistID: ", dataArtist);
        else
            console.log("Error: ", dataArtist, dataCommunity);
            
    }    

    

    prepareLink(url: string){
        console.log(Utils.prependImagesUrl(url));
        
        return Utils.prependImagesUrl(url);
    }

    isArtwork(array: any){
        if(array.artist)
            return true;
        else
            return false;
    }


    openDivComment() {
        let interval = setInterval(() => {
            if (document.body.contains(document.querySelector('.divComment'))) {
                clearInterval(interval);
                this.botoComment_click();
                this.findComment(this.route.snapshot.queryParams.comment);
            }
        }, 350);
    }

    findComment(id) {
        let interval = setInterval(() => {
            if (document.body.contains(document.getElementById(`comment-${id}`))) {
                clearInterval(interval);
                if (this.checkComment(id)) document.getElementById(`comment-${id}`).parentElement.parentElement.parentElement.style.display = 'block';
            
                document.getElementById(`comment-${id}`).scrollIntoView({
                    behavior: "smooth"
                });
                document.getElementById(`comment-${id}`).style.background = "#9929296e";
              
            }   
        }, 350);        
    }   

    checkComment(id) {
        let comment = document.getElementById(`comment-${id}`)
        let parent = comment.parentNode.parentElement;
        if (parent.className.split(' ')[0] == 'mainComment') return false;
        return true;
    }
    
    botoComment_click(){
        this.viewOpen = false;
        this.commentOpen = !this.commentOpen;
        this.ratingOpen = false;
        this.actView();
        this.actComment();
        this.actRating();
    }
    botoView_click() {
        this.viewOpen = !this.viewOpen;
        this.commentOpen = false;
        this.ratingOpen = false;
        this.actView();
        this.actComment();
        this.actRating();
    }
    botoRating_click() {
        this.viewOpen = false;
        this.commentOpen = false;
        this.ratingOpen = !this.ratingOpen;
        this.actView();
        this.actComment();
        this.actRating();
    }
    actComment(){
        let $botoComment = $(this.post.nativeElement).find(".botoComment");
        let $divComment = $(this.post.nativeElement).find(".divComment");

        $botoComment.attr("data-open", this.commentOpen);
        
        if (this.commentOpen) $divComment.slideDown(300);
        else $divComment.slideUp(300);

    }
    actView() {
        let $botoView = $(this.post.nativeElement).find(".botoView");
        let $divInfo = $(this.post.nativeElement).find(".divInfo");

        $botoView.attr("data-open", this.viewOpen);
        
        if (this.viewOpen) $divInfo.slideDown(300);
        else $divInfo.slideUp(300);
    }
    actRating() {
        let $botoRating = $(this.post.nativeElement).find(".botoRating");
        let $divRating = $(this.post.nativeElement).find(".divRating");

        $botoRating.attr("data-open", this.ratingOpen);
        
        if (this.ratingOpen) $divRating.slideDown(300);
        else $divRating.slideUp(300);
    }

    // Videos //
    marcarPostActiu() {
        // Saber a quin slide de .find(x app-publicacions) estem
        // let actiu = $(".post:in-viewport(0, app-publicacions)").first();
        // let actiu = $(".post:in-viewport").first();
        let $swiper = $("swiper.swipeContentPublicacio:in-viewport").first();
        // console.log($swiper[0]);
        if (!$swiper.length) return;
        this.swiperActiu = $swiper[0].swiper;

        let $post = $swiper.parents(".post");

        
        this.active = $post[0] == this.post?.nativeElement;

        if (this.active) {
            this.enviarViewPost();
            this.reproduirVideoPostActiu();
        }

        // if (!$post.hasClass("active")) {
        //     $post.addClass("active");
        //     $(".post").not($post).removeClass("active");
        //     this.enviarViewPost();
        //     this.reproduirVideoPostActiu();
        // }
    }

    getURLPost(){}

    enviarViewPost() {
        if (this.viewPostEnviat) return;
        
        // Si falla, bueno, que le den //
        this.main.provider.viewPost(this.p.idPost)
            // .then(
            //     data => { Utils.logVerd(this.p.idPost + " Post vist rebut"); },
            //     error => { Utils.logVermell(this.p.idPost + " Error enviant peticio post vist"); }
            // );

        // console.log("VIEW POST ENVIAT");
        this.viewPostEnviat = true;
    }
    async reproduirVideoPostActiu() {
        let $video = $(".post.active .swipeContentPublicacio .swiper-slide.swiper-slide-active video").first();

        if ($video.length) await $video[0].play();
        this.pausaAltresVideos($video);
    }
    async reproduirVideoAquestSlide(event) {
        if (this.autoplay) {
            let $video = $(event.slides).filter(".swiper-slide.swiper-slide-active").find("video").first();

            if ($video.length) await $video[0].play();
            this.pausaAltresVideos($video);
        }
    }
    video_click(e) {
        // Si cliques un video que s'estava reproduint, desactives autoplay //
        if (!$(e).prop("paused")) this.autoplay = !this.autoplay;
        // Si cliques un video que no s'estava reproduint, actives/mantens autoplay activat //
        else if (!this.autoplay) this.autoplay = true;

        this.playPauseVideo(e);
        this.pausaAltresVideos(e);
    }
    playPauseVideo(vid) {
        $(vid).trigger($(vid).prop("paused") ? "play" : "pause");
    }
    pausaAltresVideos(vid) {
        $(".post .swiper-slide video").not(vid).trigger("pause");
    }

    // Modal //
    openMoreModal() {
        // Definir opcions //
        let viewCollection = {
            accio: () => { this.main.notInMyBeta(); },
            img: "assets/in2art/icones/view-collection.svg",
            text: this.main.t("View collection"),
            class: "view",
        };
        let save = {
            accio: () => { this.main.notInMyBeta(); },
            img: "assets/in2art/icones/save.svg",
            text: this.main.t("Save"),
            class: "save",
        };
        let share = {
            // accio: () => { this.main.goToPost(this.p.idPost); },
            img: "assets/in2art/icones/share.svg",
            text: this.main.t("Share"),
            class: "share",
        };
        let copy = {
            accio: () => {
                this.closeMoreModal();
                this.main.copy(this.main.urlBase + "/post/" + this.p.idPost);
            },
            img: "assets/in2art/icones/link.svg",
            text: this.main.t("Copy URL"),
            class: "copy",
        };
        let report = {
            accio: () => {
                this.closeMoreModal();
                this.main.modalReport.openModal(this.p.idPost, 1);
            },
            img: "assets/in2art/icones/report.svg",
            text: this.main.t("Report"),
            class: "report",
        };

        // Aplicar opcions //
        let opcions = [
            viewCollection,
            save,
            share,
        ];

        if (window.isSecureContext)
            opcions.push(copy);

        if (this.main.currentUser.username != this.p.user.username) { 
            opcions.push(report);
        }

        this.main.modalOptions.openModal(opcions);

    }
    closeMoreModal() { this.main.modalOptions.closeModal(); }
}

