import { TraduccioService } from './../../../locale/traduccions/traduccio.service';
import { NotificationComponent } from './../components/notification/notification.component';
import { Toaster } from 'ngx-toast-notifications';
import { NotificationCheckerService } from './notification-checker';
import { ErrorHandlerService } from './error-handler.service';
import { ProviderService } from './provider.service';
import { ModalOptionsComponent } from '../components/modal-options/modal-options.component';
import { ModalReportComponent } from '../components/modal-report/modal-report.component';
import { Utils } from './utils.service';
import { PeticioService } from 'src/app/in2art/services/peticio.service';
import { AppBarComponent } from './../components/appbar/appbar.component';
import { SplashScreenComponent } from './../components/splash-screen/splash-screen.component';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { DomSanitizer } from '@angular/platform-browser';
import { ModalAuctionComponent } from '../components/modal-auction/modal-auction.component';

// https://developerslogblog.wordpress.com/2019/04/23/how-to-use-angular-services-to-share-data-between-components/ //

@Injectable({
    providedIn: 'root'
})
export class MainService {
    // Components //
    public appbar: AppBarComponent;
    public splashScreen: SplashScreenComponent;
    public modalOptions: ModalOptionsComponent;
    public modalReport: ModalReportComponent;
    public modalAuction: ModalAuctionComponent;

    // Services //
    public eh: ErrorHandlerService;
    public ps: PeticioService;
    public provider: ProviderService;
    public utils: Utils;
    private traductor: TraduccioService;

    public readonly debug = false;
    public urlBase = window.location.origin; // location.origin / location.pathname //
    public rutesInvalidesHistorial = ["profile", "home", "new", "interests", "auth", "login", "logout", "signup", "activate"];
    public rutesPubliques = [
        "/presentation",
        "/login",
        "/auth/login",
        "/signup",
        "/auth/signup",
        "/resetpassword",
        "/auth/resetpassword",
        "/auth/resetpassword/?token",
        "/recoverpassword",
        "/auth/recoverpassword",
        "/settings/about/privacy-policy",
        "/auth/activate?token",
        "/activate",
        "/welcome",
        "/ytvideo",
        // "/forgetPassword/sendEmail",
    ];

    public page: any;
    public params: any;

    public currentUser;
    public activateProcess: boolean = false;
    public historial = [];

    constructor(
        public router: Router,
        public toster: ToastrService,
        private toaster: Toaster,
        private sanitizer: DomSanitizer,
        public notificationChecker: NotificationCheckerService,
    ) {
        // NOMS per poder debugar //
        if (this.debug) window["main"] = this;

        this.eh = new ErrorHandlerService(this);
        this.ps = new PeticioService(this);
        this.provider = new ProviderService(this);
        this.utils = Utils;
        this.traductor = new TraduccioService(this.ps.http);
        this.onInit();
    }
    onInit() {
        // Carreguem current user de cookies, desprs farem peticio i si falla, reconduim a login o algo //
        this.currentUser = Utils.getCurrentUser();

        this.notificationChecker.start(10000);

        console.log("currentUser", this.currentUser);
        

        if (Utils.getToken()) {
            this.refreshCurrentUser();
            this.goTo("/home");
        }
    }
    newPage(_this) {
        this.page = _this;
        this.comprovarAccesPagina();
    }
    onDestroyPage() {
        // Tancar modals //
        this.modalOptions.closeModal();
        this.modalReport.closeModal();
        
        // Tancar dropdown Appbar //
        this.appbar.tooltipProfileObert = false;

        // Guardar pagina historial //
        this.afegirRuta(this.router.url);
    }
    comprovarAccesPagina() {
        // Treure idioma "/ca" //
        let pathname = window.location.pathname.replace("/" + this.getLocale(), "");
        
        // Si s ruta pblica, cap problema //
        if (this.rutesPubliques.includes(pathname)) return;
        if (pathname.includes("activate")) return;
        if (pathname.includes("verifyaccount")) return;
        if (pathname.includes("resetpassword")) return;
        if (pathname.includes("forgetPassword/sendPassword")) return;

        // Si no, restringim accs //
        if (!Utils.getToken())
            this.goTo("auth/login");
    }

    async refreshCurrentUser() {

        await this.provider.getLoginInfo().then(
            async (data: any) => {

                this.currentUser.userProfileId = data["profiles"]["viewer"]["userProfile"].id;
                this.currentUser.email = data.email;
                this.currentUser.username = data["profiles"]["viewer"].nickname;
                this.currentUser.name = data.name;
                this.currentUser.color = data.color;

                this.currentUser.photo = this.getUserPhotoPath(data["profiles"]["viewer"]["userProfile"].id).then(
                    (data: any) => {
                        return data;
                    }
                );
                
                Utils.setCurrentUser(this.currentUser);
            }).catch();
            
        Utils.setCurrentUser(this.currentUser);

    }

    activateEmailVerifyPage(){
        this.activateProcess = true;
    }

    t(key) {
        return this.traductor.traduir(key);
    }
    getLocale() {
        return this.traductor.idioma;
    }

    goTo(url) {
        if (url == undefined)
            return console.log("goTo undefined??");
        
        this.afegirRuta(url);
        this.router.navigate([url]);
    }
    afegirRuta(url) {
        url = url.replace(/^\/|\/$/g, ''); // Treure barres davant i darrere //
        if (this.rutesInvalidesHistorial.includes(url)) return;
        if (this.historial[0] == url) return;
        this.historial.unshift(url); // Si s vlida, afegir a la primera posici //
    }
    goBack() {
        this.historial.splice(0, 1); // Esborrem la primera posicio de l'array //
        let url = this.historial[0];
        this.goTo(url);
    }
    goToSignup(){this.goTo("/auth/signup");}
    goToWelcome(){this.goTo("/welcome");}
    goToHome() { this.goTo("/"); }
    goToArtwork(id) {
        if (id) this.goTo("/artwork/" + id);
        else this.goToHome();
    }
    goToPost(id) {
        if (id) this.goTo("/post/" + id);
        else this.goToHome();
    }
    gotToEmailSend(){
        this.activateProcess = true;
        this.goTo("/auth/verifyaccount");
    }
    goToLogin(){
        this.goTo("/auth/login");
    }
    goToUser(t) { this.goTo("/" + t); }
    goToMedium(t) { this.router.navigate(['/search'], { queryParams: { t: t } }); }
    goToMovement(t) { this.router.navigate(['/search'], { queryParams: { t: t } }); }
    goToTag(t) { this.router.navigate(['/search'], { queryParams: { t: t } }); }
    // goToMedium(t) { this.goTo("/medium_" + t); }
    // goToMovement(t) { this.goTo("/movement_" + t); }
    // goToTag(t) { this.goTo("/tag_" + t); }

    setParams(params) { this.params = params; }
    getParams() {
        let params = this.params;
        delete this.params;
        return params;
    }
    sanitizeVideoURL(videoURL: string) {
        return this.sanitizer.bypassSecurityTrustResourceUrl(videoURL);
    }
    public log(t) { console.log(t); }
    public logDebug(t) { if (this.debug) console.log(t); }

    copy(msg: string) {
        try {
            navigator.clipboard.writeText(msg);
            this.toastSuccess("URL copied to the clipboard");
        }
        catch { this.toastDanger("Couldn't copy URL. Try again later"); }
    }
    // Toasts //
    toastShow(msg) { this.toaster.open(this.t(msg), { type: 'dark' }); }
    toastSuccess(msg) { this.toaster.open(this.t(msg), { type: 'success' }); }
    toastDanger(msg) { this.toaster.open(this.t(msg), { type: 'danger' }); }
    toastInfo(msg) { this.toaster.open(this.t(msg), { type: 'info' }); }
    toastWarning(msg) { this.toaster.open(this.t(msg), { type: 'warning' }); }
    // 'primary' | 'secondary' | 'success' | 'danger' | 'warning' | 'info' | 'light' | 'dark' //

    toastNotificacio(notificacio) {
        this.setParams(notificacio);
        this.toaster.open(NotificationComponent);
    }

    notInMyBeta() {
        this.toastShow('Not available in beta version');
    }

    getStringBetween(str, start, end) {
        const result = str.match(new RegExp(start + "(.*)" + end));
        return result[1];
    }

    async getUserPhotoPath(userProfileId: string){
        await this.provider.getUserPhoto(userProfileId).then(
            (res: any) => {
                 this.currentUser.photo = res["picture"].path;
            }
        );
    }

}