import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { MainService } from '../../../services/main.service';

@Component({
    selector: 'app-comments',
    templateUrl: './comments.component.html',
    styleUrls: ['./comments.component.scss']
})
export class CommentsComponent implements OnInit {
    @Input() idForum;
    @ViewChild("commentComponent") commentComponent;

    public input: any;
    public loadingComments = true;
    public sendComment = false;
    public showReply = false;
    public comments: any = [];
    public userInfoToReply = { id: null, name: null, parent: null };

    constructor(public main: MainService) { }

    ngOnInit() { this.getMainComments(); }

    async getMainComments() {
        this.comments = await this.main.provider.getComments(this.idForum);
        for (const key in this.comments) {
            this.comments[key].answers = [];
            this.comments[key].totalAnswers = 0;
            if (this.comments[key].childs.length != 0) this.getChildComments(this.comments[key], null);
        }
        this.loadingComments = false;

    }

    async getChildComments(mainComment: any, idChildComment: any) {
        let childComments: any = await this.main.provider.getParent(!idChildComment ? mainComment.id : idChildComment);
        for (const key in childComments) {
            mainComment.answers.push(childComments[key]);
            mainComment.totalAnswers += 1;
            this.getChildComments(mainComment, childComments[key].id);
        }
    }

    async onSubmit() {
        if (!this.input) return;

        this.sendComment = true;

        let forumMessageId = this.showReply ? this.userInfoToReply.id : null;
        let data: any = await this.main.provider.postComment(this.input, this.idForum, forumMessageId);
        let comment = await this.main.provider.getCommentById(data.lastId);

        this.saveComments(comment[0])
        this.input = "";
        this.commentComponent = true;
        this.sendComment = false;
        this.showReply = false;
    }

    saveComments(comment) {
        //Afegeixo l'usuari perqu en la petici que faig per a obtenir el comentari noms em retorna l'aneu de l'usuari.
        comment['user'] = { id: this.main.currentUser.id, username: this.main.currentUser.username, photo: this.main.currentUser.photo }
        if (this.showReply) {
            let arrayAnswers = this.getArrayAnswers(!this.userInfoToReply.parent ? this.userInfoToReply.id : this.userInfoToReply.parent);
            arrayAnswers.answers.unshift(comment);
            arrayAnswers.totalAnswers += 1;
            return;
        }
        comment['answers'] = [];
        comment['totalAnswers'] = 0;
        this.comments.unshift(comment);
    }
    
    //Busco el array on s'ha de guardar la resposta.
    getArrayAnswers(forum_message_id) {
        for (const key in this.comments) {
            if (this.comments[key].id == forum_message_id) return this.comments[key];
            let info = this.comments[key].answers.find(answer => answer.id == forum_message_id);
            if (info != undefined) return info;
        }
    }

    onReply(idComment, parent, name) {
        if (!name) {
            this.main.toster.info("Loading comments...")
            this.showReply = false;
            return;
        }
        this.userInfoToReply = { id: idComment, name: name, parent: parent }
        this.showReply = true;
    }
} 
