import { Utils } from './../../app/in2art/services/utils.service';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class TraduccioService {

    public idioma;
    public json = null;

    constructor(private httpClient: HttpClient) {
        // Detectar idioma pagina //
        this.idioma = document.documentElement.lang.split("-")[0] || "en";
        // this.idioma = "ca"; // Comentar //

        // Carregar arxiu correcte //
        this.httpClient.get(`assets/in2art/i18n/${this.idioma}.json`).subscribe(data => { this.json = data; });
    }

    public traduir(key) {
        if (!this.json) {
            // Utils.logVermell("No s'ha carregat cap fitxer de traduccions");
            return key;
        }
        if (!this.json[key]) {
            Utils.logMagenta(`A traduccions falta la clau: "${key}"`);
            this.afegirKeyALogs(key);
            return key;
        }
        return this.json[key];
    }
    
    public afegirKeyALogs(key) {
        Utils.localStorageSet(key, new Date());
    }

}
