import { MainService } from '../../../services/main.service';
// import {DomSanitizationService} from '@angular/platform-browser';
import { Component, OnInit, Input } from '@angular/core';
import { Utils } from 'src/app/in2art/services/utils.service';

@Component({
    selector: 'app-foto-perfil',
    templateUrl: './foto-perfil.component.html',
    styleUrls: ['./foto-perfil.component.scss']
})
export class FotoPerfilComponent implements OnInit {
    
    @Input() id;
    @Input() username;
    @Input() user;
    public userImage: any;

    public readonly fotoNoImage = "assets/in2art/imatges/noimageSmall.png";
    public imagePathTest: any;
    public background;

    constructor(public main: MainService) { }

    async ngOnInit() {
        // TACAT //
        if (this.id) this.user = await this.user.id;
        if (this.username) this.user = await this.user.username;
        await this.main.provider.getUserPhoto(this.user.userProfileId).then((photo) => {
            this.imagePathTest = photo["picture"].path;  
            console.log(photo);
               
        });
        this.userImage = Utils.prependImagesUrl(this.imagePathTest);     
    }

    public getBackground() {
        return this.teFotoDefault(this.userImage) ? this.hueToHSL(this.user.color) : "none";
    }

    public hueToHSL(h) {
        if (h == null) return "hsl(0deg 0% 90%)";
        let s = 100;
        let l = 50;
        return `hsl(${h}deg ${s}% ${l}%)`;
    }
    public teFotoDefault(photo: string) {
        if (!photo) return false;
        return photo;
    }

}
