import { Router, ActivatedRoute } from '@angular/router';
import { MainService } from '../../../services/main.service';

import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Utils } from 'src/app/in2art/services/utils.service';
import { Checks } from 'src/app/in2art/services/DTOs/checks';

@Component({
    selector: 'app-recover-password',
    templateUrl: './recover-password.component.html',
    styleUrls: ['./recover-password.component.scss']
})
export class RecoverPasswordComponent implements OnInit {

    public form: FormGroup;
    public paramsUrl;
    public token: string;

    constructor(
        public main: MainService,
        private fb: FormBuilder,
        public router: Router,
        private route: ActivatedRoute,
    ) {
        main.newPage(this);

        // force route reload whenever params change;
        this.router.routeReuseStrategy.shouldReuseRoute = () => false;
        this.route.params.subscribe(params => this.paramsUrl = params);
        
        this.form = this.fb.group({
            password: ["", Validators.required],
            password2: ["", Validators.required],
        });
    }

    async ngOnInit() { 
        
         this.token = this.route.snapshot.paramMap.get("token");

    }
    ngOnDestroy() { this.main.onDestroyPage(); }


    public async submit() {
        this.form.markAllAsTouched();
        
        if (!this.form.valid) {
            console.error("Error validacio form");
            console.log("loginForm: ", this.form);
            return;
        }

        let password = Checks.EncryptPassword(this.form.controls.password.value);


        // let data = await this.main.provider.recuperarPasswordEnviarNouPassword(password, key);

        this.main.provider.recuperarPasswordEnviarNouPassword(password, this.token).then(async (res: any) => {
            console.log(res);
            
        });

    }

}
