import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';

@Injectable({
    providedIn: 'root'
})
export class Utils {

    public static readonly CADUCITAT_TOKEN_DIES = 30;

    public static readonly regexTag = /[^0-9a-zA-Z_]/g;

    constructor() { }

    public static sessionStorageSet(key, obj) {
        sessionStorage.setItem(key, JSON.stringify(obj));
    }
    public static sessionStorageGet(key) {
        try {
            return JSON.parse(sessionStorage.getItem(key));
        } catch { return null; }
    }
    public static sessionStorageRemove(key) {
        sessionStorage.removeItem(key);
    }
      

    public static cookieStorageSet(key, obj, exp = this.CADUCITAT_TOKEN_DIES) {
        this.setCookie(key, JSON.stringify(obj), exp);
    }
    public static cookieStorageGet(key) {
        try {
            return JSON.parse(this.getCookie(key));
        } catch { return null; }
    }
    public static cookieStorageRemove(key) {
        this.removeCookie(key);
    }

    public static localStorageSet(key, obj) {
        localStorage.setItem(key, JSON.stringify(obj));
    }
    public static localStorageGet(key) {
        try {
            return JSON.parse(localStorage.getItem(key));
        } catch { return null; }
    }
    public static localStorageRemove(key) {
        localStorage.removeItem(key);
    }

    // Cookies //
    public static setCookie(cname, cvalue, exdays) {
        const d = new Date();
        d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
        let expires = "expires=" + d.toUTCString();
        document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
    }
    public static getCookie(cname) {
        let name = cname + "=";
        let ca = document.cookie.split(';');
        for (let i = 0; i < ca.length; i++) {
            let c = ca[i];
            while (c.charAt(0) == ' ') {
                c = c.substring(1);
            }
            if (c.indexOf(name) == 0) {
                return c.substring(name.length, c.length);
            }
        }
        return "";
    }
    public static removeCookie(cname) {
        this.setCookie(cname, null, -1);
    }




    
    public static getToken() {
        // return "LFaHFh1P0K13pIg1uKqKXC7glAaNTlK0vLt1jJbqhVjdGTqKFIW1uUw7dq816BVi7EzLMU8svAVbDHk2UonNDPlBzdGCqevgeQ3i"; // Master token //
        
        Utils.actCaducitatToken();
        
        let currentUser = this.sessionStorageGet("currentUser") || this.cookieStorageGet("currentUser");
        if (!currentUser) return null;
        return currentUser.token;
    }
    public static actCaducitatToken() {
        let currentUser = this.cookieStorageGet("currentUser");
        if (currentUser) Utils.cookieStorageSet("currentUser", currentUser, Utils.CADUCITAT_TOKEN_DIES);
    }
    public static getCurrentUser() {
        let currentUser = this.sessionStorageGet("currentUser") || this.cookieStorageGet("currentUser");
        if (!currentUser) return null;
        return currentUser;
    }
    public static setCurrentUser(currentUser) {
        this.sessionStorageSet("currentUser", currentUser);
        if (this.cookieStorageGet("currentUser"))
            this.cookieStorageSet("currentUser", currentUser);
    }
    public static getId() {
        let currentUser = this.sessionStorageGet("currentUser") || this.cookieStorageGet("currentUser");
        if (!currentUser) return null;
        return currentUser.id;
    }
    public static getEmail() {
        try {
            let currentUser = this.sessionStorageGet("currentUser") || this.cookieStorageGet("currentUser");
            return currentUser.email;
        } catch { return null; }
    }

    public static sessionToCookie(key) {
        let value = this.sessionStorageGet(key);
        if (!value) return;
        this.localStorageSet(key, value);
        this.sessionStorageRemove(key);
    }
    public static cookieToSession(key) {
        let value = this.localStorageGet(key);
        if (!value) return;
        this.sessionStorageSet(key, value);
        this.localStorageRemove(key);
    }
    public static removeCurrentUserSession() {
        sessionStorage.removeItem("currentUser");
    }
    public static removeCurrentUserCookie() {
        this.removeCookie("currentUser");
    }



    // Utils //
    public static esMobil() {
        return window.innerWidth <= 576;
    }
    public static esPc() {
        return !this.esMobil();
    }
    public static objPle(obj) {
        if (!obj) return false;
        else return JSON.stringify(obj) != "{}";
    }
    public static treureNullsArray(array) {
        return array.filter(v => v);
    }
    public static async wait(seg) {
        return new Promise(resolve => { setTimeout(() => { resolve({}); }, seg * 1000); });
    }
    public static async waitDebug(seg) {
        this.logMagenta(`- Wait ${seg} seg START -`);
        return new Promise(resolve => { setTimeout(() => { this.logMagenta(`- Wait ${seg} seg END -`); resolve({}); }, seg * 1000); });
    }

    public static log(t) { console.log(t); }
    public static logc(t, c) { console.log("%c" + t, "color:" + c); }

    public static logBlau(t) { this.logc(t, "#00c0ff"); }
    public static logCian(t) { this.logc(t, "cyan"); }
    public static logVerd(t) { this.logc(t, "lime"); }
    public static logGold(t) { this.logc(t, "gold"); }
    public static logGroc(t) { this.logc(t, "yellow"); }
    public static logMagenta(t) { this.logc(t, "magenta"); }
    public static logVermell(t) { this.logc(t, "red"); }
    public static logTaronja(t) { this.logc(t, "orange"); }
    
    public static blobToBase64(blob) {
        const reader = new FileReader();
        reader.readAsDataURL(blob);
        return new Promise(resolve => {
            reader.onloadend = () => {
                resolve(reader.result);
            };
        });
    };
    public static dataURLToBlob(dataURL) {
        var BASE64_MARKER = ';base64,';
        if (dataURL.indexOf(BASE64_MARKER) == -1) {
            var parts = dataURL.split(',');
            var contentType = parts[0].split(':')[1];
            var raw = decodeURIComponent(parts[1]);
            return new Blob([raw], {type: contentType});
        }
        var parts = dataURL.split(BASE64_MARKER);
        var contentType = parts[0].split(':')[1];
        var raw = window.atob(parts[1]);
        var rawLength = raw.length;
        var uInt8Array = new Uint8Array(rawLength);
        for (var i = 0; i < rawLength; ++i) {
            uInt8Array[i] = raw.charCodeAt(i);
        }
        return new Blob([uInt8Array], {type: contentType});
    }
    public static prependImagesUrl(url) {
        if (url && !url.startsWith(environment.api_url))
            url = environment.images_url + url;
        return url;
    }
    public static numberInRange(n, min, max) {
        if (n < min) n = min;
        if (n > max) n = max;
        return n;
    }
    public static numberIsInRange(n, min, max) {
        return (n >= min && n <= max);
    }
    public static getInputInRange(target) {
        if (target.value == "") return "";
        return Utils.numberInRange(parseInt(target.value), parseInt(target.min), parseInt(target.max));
    }
    public static clonarObj(obj) {
        return JSON.parse(JSON.stringify(obj));
    }

    public static parseTag(t) {
        return t.replace(this.regexTag, "");
        // Caracters permesos: 0123456789abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ_
        // .normalize("NFD").replace(/\p{Diacritic}/gu, "") // Accents //
    }

    public static arrayConte(array, valor) {
        return array.includes(valor);
    }

    public static showFormData(formData) {
        let res = [];
        for (var pair of formData.entries()) {
            res.push(pair);
        }
        console.log(res);
    }
    public static romanize(num) {
        if (isNaN(num))
            return NaN;
        var digits = String(+num).split(""),
            key = ["","C","CC","CCC","CD","D","DC","DCC","DCCC","CM",
                   "","X","XX","XXX","XL","L","LX","LXX","LXXX","XC",
                   "","I","II","III","IV","V","VI","VII","VIII","IX"],
            roman = "",
            i = 3;
        while (i--)
            roman = (key[+digits.pop() + (i * 10)] || "") + roman;
        return Array(+digits.join("") + 1).join("M") + roman;
    }
    public static format00(n = 0) { return ("0" + n).slice(-2); }
    public static format000(n = 0) { return ("00" + n).slice(-3); }
    public static format0000(n = 0) { return ("000" + n).slice(-4); }




}
