    public async getLoginInfo(){
        let userLogin;
        await this.main.provider.getCurrentLoginInfo().then((res: any) => {
            if (this.main.eh.hiHaError(res)) {
                this.main.toastDanger("Error getting user info");
                return;
            }
            userLogin = res;
        
        }).catch((error) => {console.log(error)});
        return userLogin;
    }


