    public timeline_getYourTimeline(elements, isFirst) {
        return new Promise(resolve => {
            this.main.ps.get(`/yourTimeline/${elements}/${isFirst + 0}`, {},
                (data) => {
                    if (this.main.eh.hiHaError(data)) resolve(null);
                    else {
                        // console.log(Utils.clonarObj(data.rows));
                        data.rows = data.rows.filter(e => e.content.length);
                        data.rows = data.rows.map(post => this.parsePostObra(post));
                        // console.log(Utils.clonarObj(data.rows));
                        resolve(data.rows);
                    }
                },
                (error) => { this.main.eh.peticioFallada(error); resolve(null); }
            );
        });
    }
