    public async searchUsuaris(text) {
        //TODO: Canviar quan funcioni, ara aixo fa una peticio per llsitar tots els users i desprs els filtra al frontend....
        return new Promise(resolve => {
            this.main.ps.post(`/user/datatable`, {},
                (data) => {
                    if (this.main.eh.mostrarError(data)) resolve(null);
                    else {
                        let res = data.data.filter((u) => {
                            if (u.name?.includes(text)) return true;
                            if (u.lastName?.includes(text)) return true;
                            if (u.username?.includes(text)) return true;
                            return false;
                        });
                        
                        resolve(res);
                    }
                },
                (error) => { this.main.eh.peticioFallada(error); resolve(null); }
            );
        });
        // return this.getFollowing(25);
    }
    public async searchMediums(text) {
        // TODO: Evitar filtratge artificial, fer peticio especfica
        let res: any = await this.getMediums();
        res = res.filter(v => v.nom_categoria.includes(text));
        return res;
    }
    public async searchMovements(text) {
        // TODO: Evitar filtratge artificial, fer peticio especfica
        let res: any = await this.getMovements();
        res = res.filter(v => v.nom_estil.includes(text));
        return res;
    }
