    public async searchPublicacions(text, page, elements) {
        return new Promise(resolve => {
            this.main.ps.post(`/search/searchByWord/${page}/${elements}`, { word: text },
                (data) => {
                    if (this.main.eh.hiHaError(data)) resolve(null);
                    else {
                        console.log(Utils.clonarObj(data.rows));
                        data.rows = data.rows.filter(e => e.content.length);
                        data.rows = data.rows.map(p => {
                            p.user = this.parseUser(p.user);
                            return this.parsePostObra(p);
                        });
                        console.log(Utils.clonarObj(data.rows));
                        
                        resolve(data.rows);
                    }
                },
                (error) => { this.main.eh.peticioFallada(error); resolve(null); }
            );
        });

    }


