    // PARSE //
    public parseUser(user) {
        user.photo = Utils.prependImagesUrl(user.photo);
        return user;
    }
    public parsePostObra(p) {

        p.content.forEach(c => { c.url = Utils.prependImagesUrl(c.url); });

        // debug
        p.technique = p.technique || p.tecnique;

        // p.content.sort((a, b) => { return a.order - b.order });

        if (p.price && p.price != "~") {
            p.currency = p.currency || "";
            p.price = parseFloat(p.price).toFixed(2);
            p.currency = p.currency.text || p.currency;
            p.priceCurrency = p.price + p.currency;
            delete p.price;
            delete p.currency;
        }
        return p;
    }





    
