    async getVideosYoutubeIn2art() {
        let url = `https://www.googleapis.com/youtube/v3/search?key=${environment.youtube_api_key}&channelId=${environment.in2art_youtube_channelid}&part=snippet,id&order=date&maxResults=20`;
        
        return new Promise(resolve => {
            this.main.ps.http.get<any>(url, {}).subscribe({
                next: (data) => { resolve(data); },
                error: (error) => { console.log("Error Youtube:", error); resolve(null); }
            });

        });

    }
    async getVideoYoutube(videoId) {
        let url = `https://www.googleapis.com/youtube/v3/videos?key=${environment.youtube_api_key}&part=snippet&id=${videoId}`;
        
        return new Promise(resolve => {
            this.main.ps.http.get<any>(url, {}).subscribe({
                next: (data) => { resolve(data); },
                error: (error) => { console.log("Error Youtube:", error); resolve(null); }
            });

        });

    }


