import { MainService } from '../../services/main.service';
import * as $ from "jquery";

import { Component, OnInit, ViewChild } from '@angular/core';

import { ModalManager } from 'ngb-modal';

@Component({
    selector: 'app-modal-options',
    templateUrl: './modal-options.component.html',
    styleUrls: ['./modal-options.component.scss']
})
export class ModalOptionsComponent implements OnInit {

    @ViewChild("modalOptions") modal;

    public opcions;

    constructor(
        private main: MainService,
        private modalService: ModalManager,
    ) {
        main.modalOptions = this;
    }

    async ngOnInit() {
    }

    openModal(opcions) {
        if (opcions && opcions.length)
            this.opcions = opcions;
        
        this.modalService.open(this.modal, {
            size: "sm",
            modalClass: "llista",
            backdropClass: "modal-backdrop",
            hideCloseButton: true,
            centered: true,
            backdrop: true,
            animation: true,
            keyboard: true,
            closeOnOutsideClick: true,
        });
    }
    closeModal() { try { this.modalService.close(this.modal); } catch { }; }
}
